import { _decorator, Component, Node,Vec3,instantiate, tween, random } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('game')
export class game extends Component {
    start() {
        const maxX = 200;
        const minX = -200;
        const randomX = Math.random() * (maxX - minX) + minX;
        const endPosition = new Vec3(randomX, -126.931, 0);
        
        tween(this.node)
        .to(1, { position: endPosition })
        .call(()=>{
            this.node.destroy();
        })
        .start();
    }
   
    update(deltaTime: number) {
        
    }
}


